import action from '../../../components/action/action.vue'
export default {
	components: {
		action
	},
	data() {
		return {
			Font:'全部',
			font:'全部',
			list:'',
			typeList:["全部","支出","收入"],
			timelist:["全部"],
			month:'',
			page: 1,
			size: 10,
			order_type:'',
			statistics:'',
			more: true
		};
	},
	methods: {
		// 支出收入类型选择点击
		Sel(){
			this.$refs.action.actionConfig={
				list:this.typeList,
				type:0,
				isBorderColor:false,
				specClass: 'show',
			}
		},
		itemClick(index,type){
			this.Font = this.typeList[index]
			if(index == 0){
				this.order_type = ''
			}else{
				this.order_type = index
			}
			this.getData()
		},
		
		// 时间点击
		sel(){
			this.$refs.actions.actionConfig={
				list:this.timelist,
				type:0,
				isBorderColor:false,
				specClass: 'show',
			}
		},
		itemclick(index,type){
			this.font = this.timelist[index]
			if(index == 0){
				this.month = ''
			}else{
				this.month = this.font
			}
			this.getData()
		},
		// 获取近半年的年月
		getTime() {
			var data = new Date();
			var year = data.getFullYear();
			data.setMonth(data.getMonth() + 1, 1); //获取到当前月份,设置月份
			for (var i = 0; i < 6; i++) {
			  data.setMonth(data.getMonth() - 1); //每次循环一次 月份值减1
			  var m = data.getMonth() + 1;
			  m = m < 10 ? "0" + m : m;
			  this.timelist.push(data.getFullYear() + "-" + m);
			}
		},
		// 收益汇总
		all(){
			this.$store.dispatch('getOrderStatistics',{
				order_type:this.order_type,
				month:this.month
			}).then(res=>{
				this.statistics = res.data
			}).catch(err=>{})
		},
		getData(){
			this.$store.dispatch('profitList',{
				month: this.month,
				order_type: this.order_type,
				page: this.page,
				size: this.size
			}).then(res=>{
				this.list = res.data
			}).catch(err=>{})
		},
		//详情
		detail(id){
			uni.navigateTo({
				url:'detail?id='+id
			})
		},
		withdrawal(){
			uni.navigateTo({
				url:'withdraw'
			})
		}
	},
	onShow() {
		this.all()
		this.getData()
	},
	onLoad() {
		this.getTime()
	},
	onPullDownRefresh() {
		this.more = true
		this.$store.dispatch('profitList',{
			month: this.month,
			order_type: this.order_type,
			page: 1,
			size: this.size
		}).then(res=>{
			this.list = res.data
			uni.stopPullDownRefresh()
		}).catch(err=>{})
	},
	onReachBottom() {
		if (this.more) {
			this.page++
		}
		this.more = false	
		this.$store.dispatch('profitList',{
			month: this.month,
			order_type: this.order_type,
			page: this.page,
			size: this.size
		}).then(res=>{
			this.more = true;
			if (this.page > 1) {
				var dataArr = this.list;
				res.data.forEach(item => {
					dataArr.push(item)
				});
				this.list = dataArr;
			} else {
				this.list = res.data;
			}
		}).catch(err=>{})
		
	}
}
